@import url(https://fonts.googleapis.com/css?family=Comfortaa|Comic+Neue&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fira+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css?family=Comfortaa|Comic+Neue&display=swap);
@import url(https://fonts.googleapis.com/css?family=Fira+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
* {
  font-family: 'Comic Neue', cursive, sans-serif;
  scroll-behavior: smooth;
}

.vertical-align {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.subheading {
  color: grey;
  font-size: 1rem;
}

.heading {
  font-family: "Comfortaa";
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

button {
  cursor: pointer;
}

.err-txt {
  font-size: 0.8rem;
  color: red;
}

.small-txt {
  font-size: 0.8rem;
  color: grey;
}

.main-div-width {
  width: 80%;
  margin: 0 auto;
}

.gradientbg {
  background-color: #7f5a83;
  background-image: linear-gradient(315deg, #7f5a83 0%, #0d324d 74%);
}

.nav-margin {
  margin-top: 0;
}

@media (max-width: 600px) {
  .nav-margin {
    margin-top: 3.5rem;
  }
}

.admin-bg {
  background-color: rgba(0, 0, 0, 0.05);
  min-height: 100vh;
  margin-left: 150px;
}

.admin-bg article {
  width: 60%;
}

@media (max-width: 600px) {
  .admin-bg {
    margin-left: 0px;
  }
  .admin-bg article {
    width: 100%;
  }
}

.admin-home-page {
  width: 50%;
  position: relative;
}

@media (max-width: 600px) {
  .admin-home-page {
    width: 100%;
  }
}

.hover-color-change {
  color: gray;
  -webkit-transition: all  0.5s !important;
  transition: all  0.5s !important;
}

.hover-color-change:hover {
  color: black;
}

.logo {
  width: 6rem;
  height: 6rem;
  margin: 0 auto;
}

.app-heading {
  margin: auto;
  color: #ff0077;
  font-weight: bolder;
  font-family: "Comfortaa";
}

.medium-txt {
  font-size: 1.2rem;
}

.small-txt {
  font-size: 0.8rem;
}

.large-txt {
  font-size: 1.6rem;
}

.effect-btn {
  background: rgba(10, 207, 142, 0.1);
  padding: 5px 15px;
  color: black;
  font-size: 0.95rem;
  border-radius: 5px;
  font-weight: lighter;
  outline: none;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border: 1px solid wheat;
  padding: 5px 15px;
  color: wheat;
  font-weight: bold;
}

.effect-btn:focus {
  outline: none;
  border: none;
}

.effect-btn:hover {
  color: white;
  background-color: #0acf8e;
}

.effect-btn:hover {
  background-color: wheat;
  color: grey;
}

.color-btn {
  background-color: #0095f6;
  border: none;
  color: white;
  padding: 3px 12px;
  font-size: 0.95rem;
  border-radius: 5px;
  font-weight: lighter;
  cursor: pointer;
  -webkit-transition: all  0.5s;
  transition: all  0.5s;
  padding: 10px;
}

.color-btn:hover {
  background-color: rgba(255, 255, 255, 0.9);
  color: #0095f6;
}

.green-btn {
  background-color: #0acf8e;
  border: none;
  color: white;
  padding: 3px 12px;
  font-size: 0.95rem;
  border-radius: 5px;
  font-weight: lighter;
  cursor: pointer;
  -webkit-transition: all  0.5s;
  transition: all  0.5s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.green-btn:hover {
  background-color: rgba(255, 255, 255, 0.9);
  color: #0acf8e;
}

.btn {
  outline: none;
  border: none;
  background: rgba(10, 207, 142, 0.1);
  padding: 5px 15px;
  color: black;
  font-size: 0.95rem;
  border-radius: 5px;
  font-weight: lighter;
  outline: none;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  padding: 10px;
}

.btn:focus {
  outline: none;
  border: none;
}

.btn:hover {
  color: white;
  background-color: #0acf8e;
}

.hidden-btn {
  position: absolute;
  background-color: red;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  opacity: 0;
}

.dyna-flex {
  display: flex;
}

@media (max-width: 600px) {
  .dyna-flex {
    flex-direction: column;
  }
}

.dialog-details-top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0095f6;
  padding: 5px 10px;
  color: white;
}

.dialog-details-top-bar button {
  border: 1px solid whitesmoke;
  padding: 10px;
  border-radius: 10px;
  background: none;
  color: white;
  font-weight: bold;
}

.Toastify__toast {
  margin-top: 70px;
}
